.containerPelis {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.favsTitle {
  text-align: center;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
  color: rgb(0, 0, 0);
  letter-spacing: 5px;
  font-size: 2.5em;
  font-weight: 800;
  margin: 50px;
  text-decoration: underline;
}

.card {
  position: relative;
  margin: 10px;
  width: 300px;
  height: 450px;
  background-color: #000;
  overflow: hidden;
  box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.5);
}

.card .poster:before {
  content: "";
  position: absolute;
  bottom: -180px;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, #000 50%, transparent);
  transition: 0.5s;
  z-index: 1;
}
.card:hover .poster:before {
  bottom: 0px;
  cursor: pointer;
}
.card .poster {
  position: relative;
  overflow: hidden;
}
.card .poster img {
  width: 100%;
  transition: 0.5s;
}
.card:hover .poster img {
  width: 100%;
  filter: blur(5px);
  transform: translateY(-50px);
}

.details {
  position: absolute;
  padding: 20px;
  width: 100%;
  height: 100%;
  bottom: -250px;
  left: 0;
  box-sizing: border-box;
  transition: 0.5s;
  z-index: 2;
}

.card:hover .details {
  padding-top: 250px;
  bottom: 0px;
}
.details h2 {
  height: 70px;
  color: #fff;
  margin-left: 10px;
  padding: 0;
  font-size: 20px;
}
.details h2 span {
  font-size: 13px;
  color: #ffffff;
}
.tags {
  position: relative;
  margin-top: 5px;
}

.tags span {
  padding: 2px 5px;
  margin-right: 2px;
  color: #fff;
  display: inline-block;
}

.tags span.lenguaje {
  border-radius: 5px;
  background: #7206f7;
  margin-left: 10px;
}
.tags span.voted {
  margin-left: 5px;
  border-radius: 5px;
  background: #f7068b;
}

.btnLink {
  text-decoration: none;
  border: 1px solid #fff;
  background: #7206f7;
  border-radius: 5px;
  font-size: 13px;
  font-weight: 600;
  padding: 5px;
  color: #fff;
  margin: 10px;
}

.btnDelete {
  text-decoration: none;
  border: 1px solid #fff;
  background: #f01e21;
  border-radius: 5px;
  font-size: 13px;
  font-weight: 600;
  padding: 6px;
  color: #fff;
  margin: 10px 0 0 0;
}

.btnDelete:hover {
  cursor: pointer;
}
